.login-container {
  background: rgb(218, 239, 247);
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;

  padding: 40px;
}

.login {
  margin: 20px;
  width: 600px;
  padding: 60px 40px;
  border: 1px solid silver;
  border-radius: 10px;
  color: #fff;
}

.login h1 {
  color: blue;
}

button:disabled.MuiButton-root {
  background: rgb(143, 109, 109);
  color: #fff;
}
.rows {
  display: flex;
  flex-direction: row;
  padding: 12px 10px 10px 10px;
  flex-wrap: wrap;
}

.login {
  border: 3px solid rgb(42, 85, 224);
  background: #fff;
}

.rows > * {
  flex: 1 1 20%;
}

.rows input {
  min-width: 390px;
}

.rows button {
  min-width: 390px;
}

.login .header {
  padding-bottom: 20px;
}
